const phrases: any = {
    en: {
        on: 'On',
        off: 'Off'
    },
    es: {
        on: 'Encendido',
        off: 'Apagado'
    },
    pt: {
        on: 'Ligado',
        off: 'Desligado'
    },
    fr: {
        on: 'Allum',
        off: 'teint'
    },

    ru: {
        on: '',
        off: ''
    }
}

export const translate = (phrase: string, language: string) =>
    (!!phrases[language] && !!phrases[language][phrase])
        ? phrases[language][phrase] as string
        : phrase;

export default translate;